<script>
import axios from "axios";
import qs from "querystring";
// @ is an alias to /src
import Flipbook from "flipbook-vue";
export default {
  name: "Home",
  components: { Flipbook },
  data: function() {
    return {
      end: false,
      use: [],
      useAnswers: ["food"],
      hear: [],
      hearAnswers: [
        "people",
        "boats",
        "birds",
        "animals",
        "water",
        "leaves",
        "sirens",
        "airplanes",
        "cars",
      ],
      past: [],
      pastAnswers: ["people", "birds", "animals", "water", "leaves"],
      plants: [],
      plantAnswers: [
        "food",
        "capture",
        "habitat",
        "biodiversity",
        "corridors",
        "environment",
      ],
      repurposed: [],
      repurposeAnswers: ["bricks", "pavers", "poles", "stones"],
      people: [],
      peopleAnswers: [
        "exercise",
        "picnic",
        "events",
        "scooting",
        "friends",
        "food",
      ],
      bats: [],
      batsAnswers: ["dark", "food", "quiet", "safe"],
      garden: [],
      gardenAnswers: [
        "compost",
        "beehive",
        "veg",
        "beds",
        "worms",
        "fruits",
        "greens",
        "flowers",
      ],
      environment: [],
      environmentAnswers: [
        "solar",
        "conditioners",
        "pergola",
        "curtains",
        "glazing",
        "fans",
      ],
      questions: [
        [null, null], //Question 1
        [null], //Question 2
        [null], //Question 4
        [null], //Question 6
        [null], //Question 7
        [null], // Question 8
        [null, null], // Question 10
        [null, null], // Question 11
        [null], //Question 3
        [null, null, null, null], //Question 5
        [null], //Question 9
        [null, null], //Question 12
      ],
      pages: [
        null,
        "0_corner.png",
        "1_corner.png",
        "2_corner.png",
        "3_corner.png",
        "Artboard  4.png",
        "5_corner.png",
        "Artboard  6.png",
        "7_corner.png",
        "8_corner.png",
        "9_corner.png",
        "10_corner.png",
        "11_corner.png",
        "12_corner.png",
        "13_corner.png",
        "14.png",
      ],
      ui: true,
      grabbing: false,
      page: 0,
      flipbook: null,
      score: 0,
      max: 0,
    };
  },
  methods: {
    async view() {
      try {
        await axios.post("/view");
      } catch (error) {
        //console.log("error viewing");
      }
    },
    async score() {
      try {
        await axios.post(
          "/score",
          qs.stringify([
            false,
            false,
            false,
            false,
            false,
            false,
            false,
            false,
            false,
            false,
            false,
            false,
            false,
            false,
            false,
            false,
            false,
            false,
            false,
            false,
            false,
          ])
        );
      } catch (error) {
        //console.log("error scoring");
      }
    },
    areArraysEqualSets(a1, a2) {
      let superSet = {};
      for (let i = 0; i < a1.length; i++) {
        const e = a1[i] + typeof a1[i];
        superSet[e] = 1;
      }

      for (let i = 0; i < a2.length; i++) {
        const e = a2[i] + typeof a2[i];
        if (!superSet[e]) {
          return false;
        }
        superSet[e] = 2;
      }

      for (let e in superSet) {
        if (superSet[e] === 1) {
          return false;
        }
      }

      return true;
    },
    areThereNoWrongAnswers(a1, a2) {
      for (let answer of a1) {
        if (!a2.includes(answer)) {
          return false;
        }
      }
      return true;
    },
    getScore() {
      let total = 0;
      let max = 0;
      for (let question of this.questions) {
        ++max;
        let correct = true;
        for (let answer of question) {
          if (!isNaN(answer)) {
            correct = false;
          }
        }
        if (correct) ++total;
      }

      ++max;
      if (this.areArraysEqualSets(this.use, this.useAnswers)) ++total;

      ++max;
      if (this.areThereNoWrongAnswers(this.hear, this.hearAnswers)) ++total;

      ++max;
      if (this.areArraysEqualSets(this.past, this.pastAnswers)) ++total;

      ++max;
      if (this.areArraysEqualSets(this.bats, this.batsAnswers)) ++total;

      ++max;
      if (this.areArraysEqualSets(this.plants, this.plantAnswers)) ++total;

      ++max;
      if (this.areArraysEqualSets(this.repurposed, this.repurposeAnswers))
        ++total;

      ++max;
      if (this.areArraysEqualSets(this.people, this.peopleAnswers)) ++total;

      ++max;
      if (this.areArraysEqualSets(this.garden, this.gardenAnswers)) ++total;

      ++max;
      if (this.areArraysEqualSets(this.environment, this.environmentAnswers))
        ++total;

      this.score = total;
      this.max = max;
      score();
    },
    onFlipLeftEnd(page) {
      this.page = page;
      this.ui = true;
    },
    hideUI() {
      this.ui = false;
    },
    onFlipRightEnd(page) {
      this.page = page;
      if (this.page == 15) {
        this.getScore();
        this.$bvModal.show("modal-1");
        this.end = true;
      }
      this.ui = true;
    },
    checkGrab() {
      this.grabbing = this.flipbook.cursor == "grabbing";
    },
  },
  mounted() {
    window.addEventListener("mousemove", this.checkGrab);
    window.addEventListener("touchend", this.checkGrab);
    window.addEventListener("touchmove", this.checkGrab);
    this.flipbook = this.$refs["flipbook"];
    view();
  },
};
</script>

